/* General */
body {
  background-color: #191E24;
}

h1{
  font-family: 'Tiempos Headline Medium';
  font-size:120px;
  /* color:#191E24; */
  color:white;
}
h2{
  font-family:'Colfax Medium';
  font-size:60px;
  color:white;
}
h3{
  font-family:'Colfax Light';
  color:#8C89B2;
  font-size:44px;
}
h4{
  font-family:'Colfax Light';
  color:white;
  font-size:36px;
}
h5{
  font-family:'Colfax Medium';
  font-size:24px;
  color:#DE5948;

}
h6{
  font-family:'Colfax Regular';
  font-size:20px;
  color:white;

}

.highlight{
  color: #FFC200;
  font-family:'Proxima Regular';


}

@media only screen and (max-width: 800px) {
  h1{
    font-size:60px;
  }
  h2{
    font-size:30px;
  }
  h3{
    font-size:30px;
  }
}

/* Background elements */
.title-background{
  position:absolute;
  left: 0;
  right: 0;
  top:100px;
  width:100%;

  z-index:-1;

}
.title-background-img{
  width:100%;
}

/* Thumbnail.js */
.project{
  height:300px;
  width:100%;  
  border-radius:15px;
  margin-top:48px;
  transition: all 0.3s;
}

.project-title{
  font-family:'Colfax Bold';
  font-size: 24px;
  padding-left:36px;
  width: 100%; 
}
.project-category{
  font-family:'Colfax Light';
  font-size: 24px;
  padding-left:36px;
}
.project-img{
  height:100%;
  width:100%;
}
.project:hover{
  cursor:pointer;
  position: relative; 
  transform: translateY(-10px);
  transition: all 0.3s;
}
.project-details{
  position:relative;
  top:200px;
}

a{
  color:#979797;
  transition: all 0.1s;
  text-decoration: none;

}
a:hover{
  color:white;
  transition: all 0.1s;
  text-decoration: none;  
}

/* Homepage */
.header-logo{
  margin-bottom:24px;
  user-select: none;
  pointer-events: none;
  width:100px;
}
.work-section{
  /* background-color: #191E24; */
  background-color: rgb(37, 47, 63);
  padding-top:100px;
  padding-bottom:100px;
}
.plants{
  max-width:100%;
  user-select: none;
  pointer-events: none;
  display:block;
  margin:auto;
}

/* Navigation */

.navigation{
  max-width:1400px;
  margin-top:75px;
  padding-right:24px;
  padding-left:24px;
  margin-right:auto;
  margin-left:auto;
}
.nav-text{
  font-family: "Proxima Bold";
  font-size: 24px;
  color: #817F79;
  letter-spacing: 0;
  transition: cubic-bezier(0.075, 0.82, 0.165, 1);
  transition: 0.1s;
}
@media only screen and (max-width: 800px) {
  .navigation{
    padding-right:24px;
    padding-left:0px;
  }
  .nav-text{
    font-size: 20px;
  }
  h3{
    font-size:30px;
  }
}
.nav-text:hover{
  color:white;
  transition: cubic-bezier(0.075, 0.82, 0.165, 1);
  transition: 0.1s;
}
.nav-left{
  float:left;
}
.nav-right{
  float:right;
  padding-top:20px;
}
.about{
  padding-right:24px;
  margin-left:100px;

}

.title-section{
  padding-top:40px;
  padding-bottom:120px;
}